<template>
	<div id="member-mine">
		<public-header title="个人中心"></public-header>
		<public-footer :showPreLoading="showPreLoading" :menufooter="menufooter"></public-footer>
		<div class="content">
			<div class="member-header">
				<div class="header-info flex-lr">
					<div class="flex">
						<div class="avatar">
							<img :src="deliveryer.avatar" alt="" class="img-100">
						</div>
						<div class="user-info">
							<div>#{{deliveryer.id}} {{deliveryer.title}}</div>
							<div class="font-14">账户余额：¥{{deliveryer.credit2}}</div>
						</div>
					</div>
					<div class="user-status">
						<iswitch :value="deliveryer.work_status" condition-open="1" condition-close="0" slot="right-icon" text-open="接单" text-close="休息" @change="jsToggleSwitch" keys="deliveryer.work_status"></iswitch>
					</div>
				</div>
				<div class="statcenter">
					<van-row>
						<van-col span="8" class="van-hairline--right">
							<div class="font-14">今日外卖单</div>
							<div class="padding-10-t font-weight font-bold">{{stat.takeout.num}}</div>
						</van-col>
						<van-col span="8" class="van-hairline--right">
							<div class="font-14">今日跑腿单</div>
							<div class="padding-10-t font-weight font-bold">{{stat.errander.num}}</div>
						</van-col>
						<van-col span="8">
							<div class="font-14">余额</div>
							<div class="padding-10-t font-weight font-bold">¥{{deliveryer.credit2}}</div>
						</van-col>
					</van-row>
				</div>
			</div>
			<div class="block-nav van-hairline--tb">
				<div class="padding-15-l">
					<div class="block-nav-title van-hairline--bottom font-14">我的账户</div>
				</div>
				<div class="nav-list">
					<van-row>
						<van-col span="6" class="nav-item">
							<router-link tag="div" to="/pages/finance/current">
								<div class="nav-icon">
									<i class="icon icon-news_hot_light"></i>
								</div>
								<div class="text-icon">账户明细</div>
							</router-link>
						</van-col>
						<van-col span="6" class="nav-item">
							<router-link tag="div" to="/pages/finance/index">
								<div class="nav-icon">
									<i class="icon icon-refund"></i>
								</div>
								<div class="text-icon">资产</div>
							</router-link>
						</van-col>
						<van-col span="6" class="nav-item">
							<router-link tag="div" to="/pages/finance/getcashList">
								<div class="nav-icon">
									<i class="icon icon-text1"></i>
								</div>
								<div class="text-icon">提现记录</div>
							</router-link>
						</van-col>
						<van-col span="6" class="nav-item">
							<router-link tag="div" to="/pages/member/setting">
								<div class="nav-icon">
									<i class="icon icon-settings"></i>
								</div>
								<div class="text-icon">我的设置</div>
							</router-link>
						</van-col>
						<van-col span="6" class="nav-item">
							<router-link tag="div" to="/pages/comment/list">
								<div class="nav-icon">
									<i class="icon icon-comments"></i>
								</div>
								<div class="text-icon">我的评价</div>
							</router-link>
						</van-col>
						<van-col span="6" class="nav-item">
							<router-link tag="div" to="/pages/member/phonic">
								<div class="nav-icon">
									<i class="icon icon-notice"></i>
								</div>
								<div class="text-icon">语音设置</div>
							</router-link>
						</van-col>
					</van-row>
				</div>
			</div>
			<div class="block-nav van-hairline--tb">
				<div class="padding-15-l">
					<div class="block-nav-title van-hairline--bottom font-14">统计</div>
				</div>
				<div class="nav-list">
					<van-row>
						<van-col span="6" class="nav-item">
							<router-link tag="div" to="/pages/statcenter/index">
								<div class="nav-icon" >
									<i class="icon icon-calendar"></i>
								</div>
								<div class="text-icon">配送统计</div>
							</router-link>
						</van-col>
						<van-col span="6" class="nav-item" v-if="takeout_rank_status == 1">
							<router-link tag="div" to="/pages/statcenter/rank">
								<div class="nav-icon" >
									<i class="icon icon-rank"></i>
								</div>
								<div class="text-icon">外卖排行</div>
							</router-link>
						</van-col>
						<van-col span="6" class="nav-item" v-if="errander_rank_status == 1">
							<router-link tag="div" to="/pages/statcenter/erranderRank">
								<div class="nav-icon" >
									<i class="icon icon-rank"></i>
								</div>
								<div class="text-icon">跑腿排行</div>
							</router-link>
						</van-col>
						<van-col span="6" class="nav-item" v-if='0'>
							<router-link tag="div" to="/pages/statcenter/location">
								<div class="nav-icon">
									<i class="icon icon-location"></i>
								</div>
								<div class="text-icon">定位信息</div>
							</router-link>
						</van-col>
					</van-row>
					<van-row v-if="config && config.development_delivery_location == 1">
						<van-col span="6" class="nav-item">
							<div @click="onMyLocation">
								<div class="nav-icon">
									<i class="icon icon-location"></i>
								</div>
								<div class="text-icon">我的位置</div>
							</div>
						</van-col>
					</van-row>
				</div>
			</div>
			<div class="member-tips">语音技术由科大讯飞提供支持</div>
		</div>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import iswitch from "@/components/switch"
	import { Dialog } from 'vant';
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				deliveryer: {
					id: 0,
					avatar: '',
					work_status: 0,
				},
				stat: {
					errander: {
						num: 0,
					},
					takeout: {
						num: 0,
					},
					total: {
						fee: 0,
					}
				},
				takeout_rank_status: 0,
				errander_rank_status: 0,
				config: {},
				location: {},
				showPreLoading: true,
				menufooter: {},
				locationInfo: {
					location_x: '',
					location_y: '',
					addtime: '',
				},
			}
		},
		components: {
			publicHeader,
			publicFooter,
			iswitch,
			Dialog
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'delivery/member/mine/index',
					autoAssign: true,
					variable: 'deliveryer',
					data: {
						type: 'today',
						menufooter: 1
					},
					success: (res) => {
						that.stat = that.util.extend(that.stat, res.stat)
						that.takeout_rank_status = res.takeout_rank_status;
						that.errander_rank_status = res.errander_rank_status;
						that.config = res.config;
						var locationInfo = that.util.getStorage('locationInfo');
						that.locationInfo = that.util.extend(that.locationInfo, locationInfo);
					}
				});
			},
			jsToggleSwitch(data) {
				var that = this;
				that.util.jsToggleSwitch({
					vue: that,
					key: data.keys,
					value: data.value,
					url: 'delivery/member/mine/setting',
					data: {
						which: 'work_status',
					},
					key: data.keys,
					confirm: '确定变更工作状态吗？',
					success: function() {
						that.util.jsSetValue({
							vue: that,
							key: data.keys,
							value: data.value
						});

						that.util.icloudapi();
					}
				});
			},
			onMyLocation() {
				var that = this;
				getData({
					vue: that,
					url: 'delivery/member/mine/location',
					data: {},
					success: (res) => {
						that.location = res.location;
						var message = '地址：' + that.location.address + '</br>'
									+ '经度：' + that.location.location_y + '</br>'
									+ '纬度：' + that.location.location_x + '</br>'
									+ '时间：' + that.location.addtime_cn + '</br>'
						Dialog.confirm({
							title: '最近位置上传',
							message: message,
						}).then(() => {
							return;
						})
					}
				});
			}
		},
		mounted() {
			this.onLoad();

			this.util.imap();
			this.util.icloudapi();
		}
	}
</script>

<style>
	#member-mine .member-header{
		position: relative;
		overflow: hidden;
	}
	#member-mine .member-header .header-info{
		padding: 15px;
		background-color:#0EC3B3;
		overflow:hidden;
		width:100%;
	}
	#member-mine .member-header .header-info .avatar{
		width: 55px;
		height: 55px;
		background-color: #fff;
		border-radius: 100%;
		border: 2px solid #fff;
		overflow: hidden;
	}
	#member-mine .member-header .header-info .user-info{
		color: #fff;
		font-size: 16px;
		padding-left: 10px;
	}
	#member-mine .member-header .header-info .user-info div:first-child{
		padding-bottom: 5px;
	}
	#member-mine .member-header .header-info .user-status .iswitch{
		height: 24px;
		line-height: 24px;
		border: 1px solid #fff;
		border-radius: 24px;
	}
	#member-mine .member-header .header-info .user-status .iswitch.close{
		background-color: #0EC3B3;
	}
	#member-mine .member-header .header-info .user-status .iswitch .block{
		border-radius: 24px;
	}
	#member-mine .member-header .header-info .user-status .iswitch .text{
		font-size: 13px;
	}
	#member-mine .member-header .statcenter{
		padding: 15px;
		background-color: #0EC3B3;
	}
	#member-mine .member-header .statcenter .van-col{
		text-align: center;
		color: #fff;
	}
	#member-mine .block-nav{
		position: relative;
		overflow: hidden;
		margin-top: 15px;
		background-color: #fff;
	}
	#member-mine .block-nav .block-nav-title{
		font-size: 14px;
		height: 40px;
		line-height: 40px;
	}
	#member-mine .block-nav .nav-list .nav-item{
		margin: 10px 0;
		text-align: center;
	}
	#member-mine .block-nav .nav-list .nav-item .nav-icon{
		height:40px;
		line-height:40px;
		color:#767676;
	}
	#member-mine .block-nav .nav-list .nav-item .nav-icon .icon{
		font-size: 30px;
	}
	#member-mine .block-nav .nav-list .nav-item .text-icon{
		max-width:100%;
		color:#3a3a3a;
		font-size:14px;
		overflow:hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
	}
	#member-mine .block-nav .nav-list .nav-item .nav-tips{
		padding-top: 5px;
		color:#9C9C9C;
		font-size:13px;
		text-align:center;
		overflow:hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
	}
	#member-mine .member-tips{
		font-size: 14px;
		text-align: center;
		margin-top: 20px;
		color: #999;
	}
</style>


